@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
body {
  margin: 0;
  font-family: "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0%;
  box-sizing: border-box;
  position: relative;
}

::-webkit-scrollbar {
  width: 0.7rem;
}
::-webkit-scrollbar-track {
  background: linear-gradient(20deg, #4B0082, #A80074);
  border-radius: 2rem;
}

::-webkit-scrollbar{
  width: .2rem;
}

::-webkit-scrollbar-thumb {
  background: #E12E5E;
  border-radius: 2rem;
}

.continner {
  /* background: linear-gradient(20deg, #f78484, #7c127c); */
  background: linear-gradient(20deg, #4B0082, #E12E5E);
  font-family: consolas;
  color: white;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow: hidden;
}

.continner a {
  color: rgb(49, 49, 49);
  text-decoration: none;
}

.nav-list {
  display: flex;
  list-style: none;
  justify-content: space-between;
}

.nav-item {
  margin-left: 10px;

}

.nav-item a {
  position: relative;
  padding: 5px 20px 5px 40px;
  font-weight: bold;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.3s;
}

.nav-item a:hover {
  /* border: 1px solid #d3f5ce; */
  background-color: #e210a1;
  border-radius: 2rem;
  color: #272727;
  text-shadow: 1px 1px 1px white;
}

.nav-icon{
  height: 1.5rem;
  widows: auto;
  position: absolute;
  bottom: .2rem;
  left: .5rem;
}

@media screen and (max-width: 400px) {
  .nav-list {
    display: none;
  }
}

.header {
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  transition: linear 50ms;
  position: fixed;
  right: 0;
  left: 0;
  z-index: 999;
  background-color: #FFFFFF;
  box-shadow: 1px 1px 8px  black;
  border-radius: 0 0 20px 20px;
}

.header-mobile {
  height: 60vh;
  display: flex;
  text-align: center;
}

.header-mobile #logo {
  position: absolute;
  top: 2rem;
  left: 2rem;
}

.header-mobile .nav-list-mobile {
  margin-top: 5rem;
}

.header-mobile .nav-item {
  height: 4rem;
}

.header-mobile .mobile-nav-icon {
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.home-icon {
  width: 2.5rem;
  height: auto;
}

.mobile-nav-icon {
  width: 3rem;
  color: black;
  display: none;
}

@media screen and (max-width: 600px) {
  .header {
    background-color: white;
  }

  .mobile-nav-icon {
    display: block;
  }
}

#main-section {
  height: 100%;
}

@media (max-height: 700px;) {
  #main-section{
    height: 200%;
  }
}

#cv-section {
  /* background: linear-gradient(20deg, #ffee00, #ff9101); */
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.card {
  width: 20rem;
  height: 30rem;
  background-color: white;
  border-radius: 4rem 0rem 3rem 0rem;
  margin-right: 2rem;
  box-shadow: 0px 1px 10px black;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: rgb(8, 8, 8);
  transition: linear 0.3s;
}

.card:hover .info-profile {
  display: block;
}

.img-wrapper {
  width: 11rem;
  height: 11rem;
  border-radius: 50%;
  display: grid;
  place-items: center;
  background-color: rgb(0, 91, 97);
  transition: linear 0.3s;
}

.img-profile {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  -webkit-filter: sepia(10);
          filter: sepia(10);
}

.img-profile:hover {
  -webkit-filter: sepia(0);
          filter: sepia(0);
}

.big-label {
  font-size: 1.5rem;
  text-align: center;
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.info-profile {
  margin-top: 1rem;
  line-height: 2rem;
  /* display: none; */
}

.big-label span {
  font-size: 1.7rem;
  color: rgb(65, 5, 5);
}

.info {
  padding-top: 2rem;
  text-align: center;
  text-align: start;
}

.info-column {
  font-family: 'Poppins', sans-serif;
  font-size: 1.2rem;
}

.info-column .label {
  color: rgb(7, 155, 135);
  font-size: 1.5rem;
  font-family: 'Poppins', sans-serif;
}

.social-account {
  margin-top: 1rem;
  display: flex;
  justify-content: space-around;
}

.account-wrapper {
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  margin: 10px;
  transition: ease-in-out .4s;
}

.social-account-ico {
  width: 1.5rem;
  height: auto;
}

.social-account span {
  display: none;
}

.account-wrapper:hover {
  border-radius: 20px;
  padding: 2px 10px;
  color: white;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.facebook:hover {
  background-color: rgb(0, 153, 255);
}

.instagram:hover {
  background-color: rgb(250, 52, 85);
}

.github:hover {
  background-color: rgb(255, 81, 0);
}

.linkedin:hover {
  background-color: rgb(0, 247, 255);
}

.account-wrapper:hover>span {
  display: inline;
}

.experiences-article {
  background-color: white;
  /* box-shadow: 0px 1px 10px black; */
  color: black;
  padding: 2rem;
  height: 30rem;
  border-radius: 4rem 0rem 4rem 0rem;
  position: relative;
}

.experiences-article>ul {
  list-style: none;
  line-height: 1.6rem;
  font-size: 1.3rem;
}

.experiences-article>h1 {
  padding-top: 2rem;
  font-size: 2rem;
  color: rgb(7, 155, 135);
  border-bottom: 2px rgb(7, 155, 135) solid;
}

.links {
  position: absolute;
  bottom: 1.5rem;
  right: 1rem;
  display: flex;
  justify-content: space-between;
}

.links a {
  font-family: 'Poppins', sans-serif;
  margin-left: 1rem;
  padding: 0.5rem;
  padding: 0.3rem 1rem;
  border-radius: 1rem 0rem 1rem 0rem;
}

.links a:hover {
  color: black;
  background-color: rgb(7, 155, 135);
}

@media screen and (max-width: 600px) {
  #cv-section {
    height: 180vh;
  }

  .card {
    margin-right: 0rem;
  }

  .experiences-article h1 {
    font-size: 1rem;
  }

  .experiences-article ul li {
    font-size: 0.7rem;
    color: black;
  }
}

.home-section {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  height: 90vh;
  padding-top: 10vh;
}

.canvas {
  width: 350px;
  height: 450px;
  /* background-color: #D4D4F7; */
  background-color: white;
  border-radius: 20rem 2rem 7rem 0rem;
}

.shape {
  position: relative;
  top: 5rem;
}

.greeting-paragraph {
  position: relative;
  top: 10rem;
  line-height: 5rem;
  font-size: 2rem;
}

.greeting-paragraph p {
  border-bottom: 5px #FFFF00 solid;
}

.greeting-item {
  color: #FFFF00;
  text-shadow: 1px 1px 3px black;
}

.main-image {
  width: auto;
  height: 120%;
  position: relative;
  bottom: 15%;
  -webkit-filter: drop-shadow(12px 12px 25px rgba(0,0,0,0.5));
          filter: drop-shadow(12px 12px 25px rgba(0,0,0,0.5));
}

@media screen and (max-width: 600px) {
  .home-section {
    height: 90vh;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  .shape{
    display: none;
  }

  .greeting-paragraph {
    margin-top: -20rem;
    font-size: 1.5rem;
  }
}

/* This file contain stylesheet foe the contact componant
 as well as the contact form component */

#contact-section {
  height: 100vh;
  display: grid;
  place-items: center;
  grid-template-columns: 50% 50%;

}

.my-pic {
  width: 27rem;
  border-radius: 3rem;
}

.form-inputs {
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: 20px;
}

.form-inputs input,
.form-inputs textarea {
  /*margin-top: 1rem;*/
  padding: 0.5rem;
  padding-right: 10rem;
  border: none;
  border-radius: 1rem;
  font-family: consolas;
  font-size: 1.2rem;
  caret-color: blue;
  transition: all ease-in 0.3s;
}

.form-inputs input[type=submit]{
  padding: .4rem 0rem;
  background-color: rgb(4, 97, 255);
  border: none ;
  color: rgb(228, 216, 216);
}

.form-inputs input:focus,
.form-inputs textarea:focus {
  outline: none;
}


.form-inputs input[type=submit]:hover{
  background-color: #EB1371;
  color: white
}

.errorMsg {
  margin-top: 1rem;
  width: 100%;
  height: 3rem;
  color: orange;
  font-size: 1.5rem;
  font-family:  Tahoma, sans-serif;
  text-align: center;
  text-shadow: 1px 1px 5px black;
}

.errorMsg ul li::selection{
    color: black;
    background-color: pink;
}

.errorMsg ul{
  list-style: none;
  line-height: 2rem;
}

@media screen and (max-width: 600px) {
  #contact-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .my-pic{
    display: none;
  }

  .form-inputs, .email-me-text{
    width: 80%;
    margin-left: 10%;
  }

  .email-me-text{
    text-align: center;
    margin-bottom: 1rem;
  }
}

/* this is a stylesheet for all the the footer area */

footer{
  height: 35vh;
  background: #A80074;
  display: grid;
  grid-template-columns: 30% 40% auto;
  border-radius: 2rem 2rem 0 0 ;

}

/* Write the stuling of the footer nav here */
.footer-nav-list{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  list-style: none;
}

.footer-nav-item{
  margin: 1.7rem;
}

.footer-nav-item a{
  font-family: consolas;
  font-weight: bold;
  color: rgb(171, 168, 145);
}

.footer-nav-item a:hover{
  color: white;
}

.footer-social-media{
  display: grid;
  place-items: center;
  border-radius: 2rem 2rem 0 0 ;
  background-color:#C3F9FF;
}

.footer-social-media p{
  font-size: 1.2rem;
  font-weight: bold;
  color: #2F4858;
}

.footer-social-media ul{
  list-style: none;
  display: grid;
  place-items: center;
}

.footer-social-media ul li{
  margin: .5rem;
}

.footer-social-media ul li a{
  text-decoration: none;
  margin: 2rem;
  font-size: 1.2rem;
  padding: .5rem 2rem;
  border-radius: 2rem;
  font-weight: bold;
}

.footer-social-media .facebook:hover{
  color: blue;
  background-color: transparent;
}

.footer-social-media .instagram:hover{
  color: rgb(200, 0, 100);
  background-color: transparent;
}

.footer-social-media .github:hover{
  color: orange;
  background-color: transparent;
}

.footer-social-media .linkedin:hover{
  color: rgba(0, 50, 255, .9);
  background-color: transparent;
}

.hire-me-section{
  display: grid;
  place-items: center;
}

.hire-me-section P{
  position: absolute;
  bottom: 1rem;
  font-size: .8rem;
  text-align: center;
}

.emailBtn{
  color: rgb(137, 140, 128) !important ;
  margin-bottom: 3rem;
  border-radius: 2rem ;
  font-weight: bold;
  font-family: sans-serif;
  padding: 1rem 2rem;
  text-transform: none;
  background-color: rgb(4, 97, 255);
}

.emailBtn:hover{
  color: rgb(255, 255, 255) !important;
  background-color: rgb(1, 144, 136);
}

@media screen and (max-width: 600px) {

    footer{
      grid-template-columns: 25% 75%;
    }

    .footer-social-media{
      border-radius: 0;
    }

    .hire-me-section{
      display: none;
    }


}

