/* This file contain stylesheet foe the contact componant
 as well as the contact form component */

#contact-section {
  height: 100vh;
  display: grid;
  place-items: center;
  grid-template-columns: 50% 50%;

}

.my-pic {
  width: 27rem;
  border-radius: 3rem;
}

.form-inputs {
  display: grid;
  grid-template-columns: 100%;
  grid-row-gap: 20px;
}

.form-inputs input,
.form-inputs textarea {
  /*margin-top: 1rem;*/
  padding: 0.5rem;
  padding-right: 10rem;
  border: none;
  border-radius: 1rem;
  font-family: consolas;
  font-size: 1.2rem;
  caret-color: blue;
  transition: all ease-in 0.3s;
}

.form-inputs input[type=submit]{
  padding: .4rem 0rem;
  background-color: rgb(4, 97, 255);
  border: none ;
  color: rgb(228, 216, 216);
}

.form-inputs input:focus,
.form-inputs textarea:focus {
  outline: none;
}


.form-inputs input[type=submit]:hover{
  background-color: #EB1371;
  color: white
}

.errorMsg {
  margin-top: 1rem;
  width: 100%;
  height: 3rem;
  color: orange;
  font-size: 1.5rem;
  font-family:  Tahoma, sans-serif;
  text-align: center;
  text-shadow: 1px 1px 5px black;
}

.errorMsg ul li::selection{
    color: black;
    background-color: pink;
}

.errorMsg ul{
  list-style: none;
  line-height: 2rem;
}

@media screen and (max-width: 600px) {
  #contact-section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .my-pic{
    display: none;
  }

  .form-inputs, .email-me-text{
    width: 80%;
    margin-left: 10%;
  }

  .email-me-text{
    text-align: center;
    margin-bottom: 1rem;
  }
}
